import React from 'react'

export default () => (
    <div style={{ textAlign: 'justify' }}>
        <h1 style={{ textAlign: 'center', marginTop: 0 }}>
            Bem-vindo  Explorao Espacial! 
        </h1>
        <p>Este  um jogo muito simples e divertido de jogar. Nele, voc  um explorador espacial que procura tesouros interplanetrios e acaba de chegar a um novo planeta. Voc precisa pousar sua nave o mais prximo possvel do tesouro. Mas voc s conhece a localizao exata do tesouro aps o pouso! Portanto, a nica ferramenta que voc tem  a sua prpria intuio.</p>
        <p style={{ textDecoration: "underline"}}>Voc pode girar livremente o planeta e at dar zoom. <b>Para girar o planeta, clique, segure e arraste.</b> Para definir seu local de pouso, clique duas vezes. Quanto mais preciso voc for, melhor.  altamente recomendvel que voc use o mouse para uma melhor experincia com o jogo.</p>
        <p>Assim que voc define seu local de pouso, a localizao exata do tesouro  revelada, juntamente com uma pontuao. Quanto mais prximo do tesouro voc pousar, mais precisa ser sua tentativa e, portanto, maior ser sua pontuao. A pontuao mxima  de 100 pontos.</p>
        <p>Cada sesso do jogo  composta de 20 tentativas, mas voc pode fazer quantas sesses desejar. Quanto mais voc joga, mais voc pratica a sua intuio!</p>
        <p>No final de cada sesso, voc encontrar uma tabela de pontuao. A pontuao de cada uma de suas 20 tentativas  apresentada. A ltima linha da tabela representa sua pontuao final. Ela varia de 0 a 100 pts. Quanto maior a pontuao final, mais prximos do tesouro foram seus pousos, de uma maneira geral.</p>
        <p>Relaxe e deixe sua intuio gui-lo a cada clique!</p>
        <p>Comece agora e divirta-se!</p>
    </div>
)


